body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
.Board{
  max-width: 1200px;
  max-height: 600px;
  pointer-events: none;
  position: absolute;
  left: 0;
  top: 0;
}
.Figure{
  max-width: 75px;
  max-height: 50%;
}

.container {
  position:relative;
}

.bQ {
  position: absolute;
  left: 225px;
  top: 0;
}
.bK {
  position: absolute;
  left: 300px;
  top: 0;
}
.bP {
  position: absolute;
  left: 300px;
  top: 75px;
}
